import {PsoClasses, PsoGenders, PsoRaces, PsoProfessions, PsoSectionIds} from "@/enums/PsoEnums"

export default class PsoCharacter {
    //Fields ===========================================================================
    private _id: number
    private _name: string;
    private _level: number;
    private _profession: PsoProfessions;
    private _race: PsoRaces;
    private _class: PsoClasses;
    private _gender: PsoGenders;
    private _sectionId: PsoSectionIds;

    private _powMatGoal: number;
    private _defMatGoal: number;
    private _evpMatGoal: number;
    private _mndMatGoal: number;
    private _luckMatGoal: number;
    private _hpMatTotal: number;
    private _tpMatTotal: number;
    private _powMatTotal: number;
    private _defMatTotal: number;
    private _evpMatTotal: number;
    private _mndMatTotal: number;
    private _luckMatTotal: number;

    //Methods ===========================================================================

    randomSectionId(): PsoSectionIds {
        const randomIndex:number = Math.floor(Math.random() * Object.keys(PsoSectionIds).length);
        switch(randomIndex){
            case 0: return PsoSectionIds.BLUEFULL;
            case 1: return PsoSectionIds.GREENILL;
            case 2: return PsoSectionIds.ORAN;
            case 3: return PsoSectionIds.PINKAL;
            case 4: return PsoSectionIds.PURPELENUM;
            case 5: return PsoSectionIds.REDRIA;
            case 6: return PsoSectionIds.SKYLY;
            case 7: return PsoSectionIds.VIRIDIA;
            case 8: return PsoSectionIds.YELLOWBOZE;
            default: return PsoSectionIds.WHITILL;
        }
    }

    clamp(input: number, min: number, max: number){
        if(input <= min){
            return min;
        } else if (input >= max){
            return max;
        } else {
            return input;
        }
    }

    getTpMatGoalForProfession(thisProfession: PsoProfessions): number {
        switch(thisProfession){
            case PsoProfessions.HUCAST: return 0;
            case PsoProfessions.HUCASEAL: return 0;
            case PsoProfessions.RACAST: return 0;
            case PsoProfessions.RACASEAL: return 0;
            default: return 125;
        }
    }

    getMixedMatAllowedForProfession(thisProfession: PsoProfessions): number {
        switch(thisProfession){
            case PsoProfessions.HUMAR: return 250;
            case PsoProfessions.RAMAR: return 250;
            case PsoProfessions.RAMARL: return 250;
            case PsoProfessions.FOMAR: return 250;
            case PsoProfessions.FOMARL: return 250;
            default: return 150;
        }
    }

    //getters and setters ===========================================================================
    constructor() {
        this._id = Date.now(); //just use epoch for now
        this._name = "New Character";
        this._level = 1;
        this._profession = PsoProfessions.HUMAR;
        this._race = PsoRaces.HUMAN;
        this._class = PsoClasses.HUNTER;
        this._gender = PsoGenders.MALE;
        this._sectionId = this.randomSectionId();
        this._powMatGoal = 0;
        this._defMatGoal = 0;
        this._evpMatGoal = 0;
        this._mndMatGoal = 0;
        this._luckMatGoal = 0;
        this._hpMatTotal = 0;
        this._tpMatTotal = 0;
        this._powMatTotal = 0;
        this._defMatTotal = 0;
        this._evpMatTotal = 0;
        this._mndMatTotal = 0;
        this._luckMatTotal = 0;
    }

    //GETTERS    ======================================
    get id(): number { return this._id; }
    get name(): string { return this._name; }
    get level(): number { return this._level; }
    get profession(): PsoProfessions { return this._profession; }
    get race(): PsoRaces { return this._race; }
    get class(): PsoClasses { return this._class; }
    get gender(): PsoGenders { return this._gender; }
    get sectionId(): PsoSectionIds { return this._sectionId; }

    get powMatGoal(): number { return this._powMatGoal }
    get defMatGoal(): number { return this._defMatGoal }
    get evpMatGoal(): number { return this._evpMatGoal }
    get mndMatGoal(): number { return this._mndMatGoal }
    get luckMatGoal(): number { return this._luckMatGoal }
    get hpMatTotal(): number { return this._hpMatTotal }
    get tpMatTotal(): number { return this._tpMatTotal }
    get powMatTotal(): number { return this._powMatTotal }
    get defMatTotal(): number { return this._defMatTotal }
    get evpMatTotal(): number { return this._evpMatTotal }
    get mndMatTotal(): number { return this._mndMatTotal }
    get luckMatTotal(): number { return this._luckMatTotal }
    
    get hpMatGoal(): number {
        return 125;
    }

    get tpMatGoal(): number {
        if(this._race !== PsoRaces.ANDROID){
            return 125;
        } else {
            return 0;
        }
    }

    get mixedMatAllowed(): number {
        if(this._race !== PsoRaces.HUMAN){
            return 150;
        } else {
            return 250;
        }
    }

    get mixedMatUnplanned(): number {
        return this.mixedMatAllowed - (
            this.powMatGoal +
            this.defMatGoal +
            this.evpMatGoal +
            this.mndMatGoal +
            this.luckMatGoal
        )
    }

    get mixedMatUnused(): number {
        return this.mixedMatAllowed - (
            this.powMatTotal +
            this.defMatTotal +
            this.evpMatTotal +
            this.mndMatTotal +
            this.luckMatTotal
        )
    }

    //SETTERS    ======================================
    set id(value: number) { this._id = value; }
    set name(value: string) { this._name = value; }
    set level(value: number) { this._level = value; }
    set profession(value: PsoProfessions) {
        this._profession = value;
        switch(value){
            case PsoProfessions.HUMAR:
                this.race = PsoRaces.HUMAN;
                this.class = PsoClasses.HUNTER;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.HUNEWEARL:
                this.race = PsoRaces.NEWMAN;
                this.class = PsoClasses.HUNTER;
                this.gender = PsoGenders.FEMALE;
                break;
            case PsoProfessions.HUCAST:
                this.race = PsoRaces.ANDROID;
                this.class = PsoClasses.HUNTER;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.HUCASEAL:
                this.race = PsoRaces.ANDROID;
                this.class = PsoClasses.HUNTER;
                this.gender = PsoGenders.FEMALE;
                break;
            case PsoProfessions.RAMAR:
                this.race = PsoRaces.HUMAN;
                this.class = PsoClasses.RANGER;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.RAMARL:
                this.race = PsoRaces.HUMAN;
                this.class = PsoClasses.RANGER;
                this.gender = PsoGenders.FEMALE;
                break;
            case PsoProfessions.RACAST:
                this.race = PsoRaces.ANDROID;
                this.class = PsoClasses.RANGER;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.RACASEAL:
                this.race = PsoRaces.ANDROID;
                this.class = PsoClasses.RANGER;
                this.gender = PsoGenders.FEMALE;
                break;
            case PsoProfessions.FOMAR:
                this.race = PsoRaces.NEWMAN;
                this.class = PsoClasses.FORCE;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.FOMARL:
                this.race = PsoRaces.HUMAN;
                this.class = PsoClasses.FORCE;
                this.gender = PsoGenders.FEMALE;
                break;
            case PsoProfessions.FONEWM:
                this.race = PsoRaces.NEWMAN;
                this.class = PsoClasses.FORCE;
                this.gender = PsoGenders.MALE;
                break;
            case PsoProfessions.FONEWEARL:
                this.race = PsoRaces.NEWMAN;
                this.class = PsoClasses.FORCE;
                this.gender = PsoGenders.FEMALE;
                break;
        }
    }
    set race(value: PsoRaces) { this._race = value; }
    set class(value: PsoClasses) { this._class = value; }
    set gender(value: PsoGenders) { this._gender = value; }
    set sectionId(value: PsoSectionIds) { this._sectionId = value; }

    set powMatGoal(value: number) { this._powMatGoal = value }
    set defMatGoal(value: number) { this._defMatGoal = value }
    set evpMatGoal(value: number) { this._evpMatGoal = value }
    set mndMatGoal(value: number) { this._mndMatGoal = value }
    set luckMatGoal(value: number) { this._luckMatGoal = value }
    set hpMatTotal(value: number) { this._hpMatTotal = value }
    set tpMatTotal(value: number) { this._tpMatTotal = value }
    set powMatTotal(value: number) { this._powMatTotal = value }
    set defMatTotal(value: number) { this._defMatTotal = value }
    set evpMatTotal(value: number) { this._evpMatTotal = value }
    set mndMatTotal(value: number) { this._mndMatTotal = value }
    set luckMatTotal(value: number) { this._luckMatTotal = value }
}