import Vue from 'vue';
import Vuex from 'vuex';
import PsoCharacter from '../models/PsoCharacter';

Vue.use(Vuex)

function processPsoCharacters(localPsoChars: any[]): Array<PsoCharacter>{
  const newList:Array<PsoCharacter> = [];
  for(const char of localPsoChars){
    let thisChar = new PsoCharacter();
    thisChar.id = char._id;
    thisChar.name = char._name;
    thisChar.level = char._level;
    thisChar.powMatGoal = char._powMatGoal;
    thisChar.defMatGoal = char._defMatGoal;
    thisChar.evpMatGoal = char._evpMatGoal;
    thisChar.mndMatGoal = char._mndMatGoal;
    thisChar.luckMatGoal = char._luckMatGoal;
    thisChar.hpMatTotal = char._hpMatTotal;
    thisChar.tpMatTotal = char._tpMatTotal;
    thisChar.powMatTotal = char._powMatTotal;
    thisChar.defMatTotal = char._defMatTotal;
    thisChar.evpMatTotal = char._evpMatTotal;
    thisChar.mndMatTotal = char._mndMatTotal;
    thisChar.luckMatTotal = char._luckMatTotal;

    thisChar.profession = char._profession;
    thisChar.race = char._race;
    thisChar.class = char._class;
    thisChar.gender = char._gender;
    thisChar.sectionId = char._sectionId;

    newList.push(thisChar);
  }

  return newList
}

export default new Vuex.Store({
  state: {
    psoCharacters: Array<PsoCharacter>()
  },
  mutations: {
    //These modify state
    addPsoCharacter(state){
      state.psoCharacters.push(new PsoCharacter());
    },
    storePsoCharacters(state){
      localStorage.setItem('psoCharacters', JSON.stringify(state.psoCharacters));
    },
    initializeStore(state){
      if (localStorage.getItem('psoCharacters')) {
        const localPsoCharacters = JSON.parse(localStorage.getItem('psoCharacters')!);
        state.psoCharacters = processPsoCharacters(localPsoCharacters);
      }
    }
  },
  actions: {
    //These ask vuex to modify state
    pleaseAddPsoCharacter(context){
      context.commit('addPsoCharacter');
    },
    pleaseStorePsoCharacters(context){
      //super not optimal, however since we need to load this extra special
      //i failed to ues vuex-persist :(
      context.commit('storePsoCharacters');
    }
  }
})
