import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "about" */ '@/views/Home/Home.vue')
  },
  {
    path: '/wh-dice',
    name: 'whDice',
    component: () => import(/* webpackChunkName: "whDice" */ '@/views/WHDiceRoller/WHDiceRoller.vue')
  },
  {
    path: '/crucible-encounters',
    name: 'crucibleEncounters',
    component: () => import(/* webpackChunkName: "crucibleEncounters" */ '@/views/CrucibleEncounters/CrucibleEncounters.vue')
  },
  {
    path: '/wood-calculator',
    name: 'woodCostCalculator',
    component: () => import(/* webpackChunkName: "woodCostCalculator" */ '@/views/WoodCostCalculator/WoodCostCalculator.vue')
  },
  {
    path: '/pso-character-planner',
    name: 'psoCharacterPlanner',
    component: () => import(/* webpackChunkName: "psoCharacterPlanner" */ '@/views/PsoCharacterPlanner/PsoCharacterPlanner.vue')
  },
  {
    //would be better as child route but isn't playing nice
    path: '/pso-character-planner/:characterId',
    props: true,
    name: 'psoCharacterView',
    component: () => import(/* webpackChunkName: "psoCharacterPlanner" */ '@/views/PsoCharacterPlanner/PsoCharacterView/PsoCharacterView.vue'),
  }
]

const router = new VueRouter({
  routes
})

export default router
